body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width:80%;
  margin-left: 10%;
  margin-top: 10px;
}

.App {
  /*text-align: center;*/
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.App {
  margin: auto;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 300;
  /*text-align: center;*/
}

.section {
  text-align: left;
  vertical-align: top;
  font-size: small;
  font-weight: 300;
}

.player-wrapper {
  width: 480px;
  height: 20px;
}

table {

}

button {
  margin-right: 10px;
}

input[type="range"]{
  height:20px;
  width: 60em;
  margin:0;
}

input[type="checkbox"]{
  height:20px;
  margin:0;
}

input[type="file"]{
  height:20px;
  width: 60em;
  margin:0;
}

.input{
  height:20px;
  width: 60em;
  margin:0;
}

progress {
  height:20px;
  width: 60em;
  margin:0;
}

.react-player {
  margin-bottom: 10px;
  background: rgba(0, 0, 0, .1);
}

.faded {
  color: rgba(0, 0, 0, .5);
}

tr {
  height: 28px;
}


.sentence {
  margin: 50px 0 0 0;
}

.sentence input {
  height: 30px;
  font-size: 18px;
  text-align: center;
  margin: 0 5px 10px 0px;
  border: none;
  background-color: lightgrey;
}

span {
  font-size: 16px;
  margin: 0 5px 10px 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.wrong {
  color:red;
}

.unknown {
  color: aquamarine;
}

.correct {
  color:green;
}

.summary {
  padding-top: 20px;
  padding-bottom: 30px;
}

.words {
  font-weight: 300;
}
